<template>
  <div id="app-container" style="min-height: 100%">
    <a-layout class="layout" style="height: 100%; min-height: 100vh">
      <template v-if="isDisplayHeader()">
        <a-layout-header class="d-flex align-items-between">
          <div class="d-flex flex-grow-1 align-items-center">
            <router-link :to="{ name: 'Index' }" class="mx-3">
              <img
                src="./assets/icons/ms-icon-150x150.png"
                height="30"
                alt="site-logo"
              />
            </router-link>
            <a-menu theme="dark" mode="horizontal">
              <a-menu-item key="1">
                <router-link :to="{ name: 'Anime Index' }">
                  動漫首頁
                </router-link>
              </a-menu-item>
            </a-menu>
          </div>
          <div class="d-flex align-items-center">
            <template v-if="user.length == 0">
              <div class="px-2 text-white">你好</div>
              <a-button type="primary" class="mx-1" @click="login">
                登入
              </a-button>
            </template>
            <template v-else>
              <div class="px-2 text-white">你好，{{ user }}</div>
              <a-button type="primary" class="mx-1" @click="logout">
                登出
              </a-button>
            </template>
          </div>
        </a-layout-header>
      </template>
      <a-layout-content style="min-height: calc(100% - 134px)">
        <div class="p-5">
          <router-view />
        </div>
      </a-layout-content>
      <a-layout-footer class="app-footer">
        <div class="d-flex justify-content-center">
          <span class="text-center">ACGL Power By&nbsp;</span>
          <a href="https://anilist.co/">AniList</a>
        </div>
      </a-layout-footer>
    </a-layout>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      user: this.$store.state.auth.user,
      avatar: this.$store.state.auth.avatar,
      page_opened_at: new Date()
    }
  },
  methods: {
    login() {
      this.$store.dispatch("auth/login")
    },
    logout() {
      this.$store.dispatch("auth/logout")
    },
    isDisplayHeader() {
      const blacklist_page_name = [
        'Auth Login Success'
      ]
      return !blacklist_page_name.includes(this.$route.name)
    },
  }
};
</script>

