const authStoreModule = {
  namespaced: true,
  state: {
    access_token: (localStorage.getItem('expired_at') && localStorage.getItem('expired_at') > Date.now()) ? (localStorage.getItem('access_token') || '') : '',
    user: (localStorage.getItem('expired_at') && localStorage.getItem('expired_at') > Date.now()) ? (localStorage.getItem('user') || '') : '',
    avatar: (localStorage.getItem('expired_at') && localStorage.getItem('expired_at') > Date.now()) ? (localStorage.getItem('avatar') || '') : '',
    expired_at: localStorage.getItem('expired_at') || 0,
    is_authenticated: localStorage.getItem('expired_at') && localStorage.getItem('expired_at') > Date.now() && localStorage.getItem('user') && localStorage.getItem('user').length > 0
  },
  mutations: {
    login (state) {
      state.access_token = localStorage.getItem('access_token') || ''
      state.user = localStorage.getItem('user') || ''
      state.avatar = localStorage.getItem('avatar') || ''
      state.expired_at = localStorage.getItem('expired_at') || 0
      location.reload()
    },
    logout (state) {
      localStorage.removeItem('access_token')
      localStorage.removeItem('user')
      localStorage.removeItem('avatar')
      localStorage.removeItem('expired_at')
      state.access_token = ''
      state.user = ''
      state.avatar = ''
      state.expired_at = 0
      location.reload()
    },
  },
  actions: {
    login ({ commit }) {
      const login_window = window.open('https://anilist.co/api/v2/oauth/authorize?client_id=' + process.env.VUE_APP_ANILIST_APP_ID + '&response_type=token', '_blank', 'width=400,height=600');
      var timer = setInterval(() => {
        if (login_window.closed) {
          clearInterval(timer);
          commit('login')
        }
      }, 1000)
    },
    logout ({ commit }) {
      commit('logout')
    },
  },
}

export { authStoreModule };